$pom: #d63e64;
$pom_inner: #ffe6ea;
$pom_bud: #2f3333;
$pom_shadow: #e4e890;

@mixin pom_top {
	width: 0;
	height: 0;
	border-left: 15px solid transparent;
	border-right: 15px solid transparent;
	border-bottom: 30px solid $pom;
}

@mixin center_flex {
	display: flex;
	justify-content: center;
	align-items: center;
}

.pom_container {
	// display: flex;
	// flex-direction: column;
	// justify-content: center;
	// align-items: center;
	// background-color: #fbffab;
	// height: 550px;
}

.total_pom {
	transform: translate(-43px, 10px);
}

.pom_base {
	position: relative;
	z-index: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 240px;
	width: 245px;
	border-radius: 50%;
	background-color: $pom;
}

.pom_inner {
	height: 215px;
	width: 220px;
	border-radius: 50%;
	background-color: $pom_inner;
}

.total_pom {
	@include center_flex;
}

.pom_inner_line {
	z-index: 1;
	position: relative;
	width: 15px;
	height: 230px;
	transform: translate(102px, -50px);
	background-color: $pom;
}

.pom_top {
	z-index: 1;
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	transform: translate(169px, -132px);
}

.tops {
	z-index: 1;
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.pom_top_base {
	background: $pom;
	height: 25px;
	width: 50px;
}

.pom_top1 {
	@include pom_top();
	transform: rotate(-35deg)translate(6px, 7px);
}

.pom_top2 {
	@include pom_top();
	transform: translate(0, 1px);
}

.pom_top3 {
	@include pom_top();
	transform: rotate(35deg)translate(-6px, 7px);
}

// POM SEEDS

// See Container
.all_pom_seeds {
	position: relative;
	z-index: 2;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	transform: translate(0, 3px);
}

// Universal pom seed style
.pom_seeds {
	height: 25px;
	width: 25px;
	border-radius: 50%;
	margin: 5.5px;
}

