// Seed color Assignments
// Seed One Color
.pom_seed1 {
	animation: seed_color 5s infinite, scale_up 3s infinite;
}

.pom_seed2 {
	animation: seed_color 5s infinite, scale_down 3s infinite;
	animation-delay: 1s;
}

.pom_seed3 {
	animation: seed_color 5s infinite, scale_down 3s infinite;
	animation-delay: 1s;
}

// Left outer Set
.pom_seeds1 {
	transform: translate(2px, 35px);
}

// Set 2
.pom_seeds2 {
	transform: translate(-12px, 5px);
}

// Center Set
.pom_seeds3 {
	transform: translate(-5px, -10px);
}

// Set 4
.pom_seeds4 {
	transform: translate(0px, 5px);
}

.pom_seeds5 {
	transform: translate(-13px, 35px);
}

// KEYFRAMES
@keyframes scale_up {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(1.2);
	}

	100% {
		transform: scale(1);
	}
}

@keyframes scale_down {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(.6);
	}

	100% {
		transform: scale(1);
	}
}

// Change Seed Color
@keyframes seed_color {
	0% {
		background-color: #ff8092;
	}
	25% {
		background-color: $pom;
	}

	50% {
		background-color: #822445;
	}

	75% {
		background-color: $pom;
	}
	100% {
		background-color: #ff8092;
	}
}
